import clsx from 'clsx';
import React, { useEffect, useState } from 'react';
import { KTIcon } from '../../../_metronic/helpers';
import { getLayout, LayoutSetup, useLayout } from '../../../_metronic/layout/core';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from 'react-router-dom';
import { addDatasourcemonitor } from '../../redux/actions';
import {connect} from 'react-redux';

const CreateMonitoringDashboard = (props) => {
  const history = useNavigate();
    const[values,setValues]=useState({
      name:'',
      datasource:'',
      categories:'CRITICAL',
      query:''
    })
    const[errors,setErrors]=useState({
      name:'',
      datasource:'',
      categories:'',
      query:''
    })
    useEffect(()=>{
      console.log("create useeffe", props?.addDatasourcemonitor())
     
    },[])
    const handleChange = (e) => {
      const {name, value} = e.target
      setValues({...values, [name]: value})
    }
  
    const Validation = () => {
      let formIsValid = true
      let errors = {}
  
      if (!values.name) {
        formIsValid = false
        errors.name = 'Enter a name'
      } else {
        errors.name = ''
      }
  
      if (!props?.datasourcemonitor) {
        formIsValid = false
        errors.datasource = 'Enter a datasource'
      } else {
        errors.datasource = ''
      }
      if (!values.categories) {
        formIsValid = false
        errors.categories = 'Enter a categories'
      } else {
        errors.categories = ''
      }
      if (!values.query) {
        formIsValid = false
        errors.query = 'Enter a query'
      } else {
        errors.query = ''
      }
      setErrors(errors)
      return formIsValid
    }
      const createHandler=(e)=>{
        e.preventDefault()
       
     
        if (Validation()) {
          let submitarray=[];
          let submitvalues = {} 
          submitvalues.name = values.name
          submitvalues.datasourceName=props?.datasourcemonitor?.name
          submitvalues.datasourceId = props?.datasourcemonitor?.datasourceId
          submitvalues.categories=values.categories
          submitvalues.query=values.query
          submitvalues.clientId="client1";
          submitvalues.intervalType="TIME";
          submitvalues.intervalValue=50
          // submitvalues.intervalType="EVENT";
          // submitvalues.intervalValue=0
          submitarray.push(submitvalues)
          console.log("sub",submitvalues,Validation(),props?.datasourcemonitor)
        fetch('http://localhost:8081/api/monitors', {
          body: JSON.stringify(submitarray),
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
          method: 'POST',
        })
        .then((response) => {
         console.log("response",response)
         if(response.status==201){
          setTimeout(() => {
            history('/dashboard/monitoring-dashboard/view-monitoringdashboard/'); // Hide the message after the delay
          }, 1500);
          toast.success(values.name+" added successfully")
          props?.addDatasourcemonitor()
          setTimeout(() => {
          
          }, 1500)
          
         }
         else{
          toast.error("failed")
         }
        })
       
        .catch((error) => {
          console.error('Error fetching data:', error);
        });
      }
      else{
        console.log("Validation failed")
      }
      }
    return (<>
   
     <div id="kt_app_toolbar" className="app-toolbar py-3 py-lg-6">
      <div
        id="kt_app_toolbar_container"
        className="app-container d-flex flex-stack container-fluid"
        style={{paddingLeft:'3px',
        paddingRight:'3px'
       }}
      >
        <div
          id="kt_page_title"
          data-kt-swapper="true"
          data-kt-swapper-mode="prepend"
          data-kt-swapper-parent="{default: '#kt_content_container', 'lg': '#kt_toolbar_container'}"
          className="page-title d-flex flex-wrap me-3 flex-column justify-content-center"
        >
          <h1 className="page-heading d-flex text-gray-900 fw-bold fs-3 my-0 flex-column justify-content-center">
          Create Monitor
          </h1>
          <ul className="breadcrumb breadcrumb-separatorless fw-semibold fs-7 my-0 pt-1">
  {/*begin::Item*/}
  <li className="breadcrumb-item text-muted">
    <a
      href="/metronic8/demo1/../demo1/index.html"
      className="text-muted text-hover-primary"
    >
      Home{" "}
    </a>
  </li>
 

  <li className="breadcrumb-item">
    <span className="bullet bg-gray-500 w-5px h-2px" />
  </li>
  <li className="breadcrumb-item text-muted">Dashboard</li>
  <li className="breadcrumb-item">
    <span className="bullet bg-gray-500 w-5px h-2px" />
  </li>
  <li className="breadcrumb-item text-muted">Monitoring Dashboard</li>
  <li className="breadcrumb-item">
    <span className="bullet bg-gray-500 w-5px h-2px" />
  </li>
  <li className="breadcrumb-item text-muted">Create Monitor</li>
</ul>
        </div>
  
      </div>
    </div>
      <div className="card">
  {/*begin::Card header*/}
  <div className="card-header">
    {/*begin::Card title*/}
    <div className="card-title fs-3 fw-bold">Create Monitor</div>
    {/*end::Card title*/}
  </div>
  {/*end::Card header*/}
  {/*begin::Form*/}
  <form id="kt_project_settings_form" className="form">
    {/*begin::Card body*/}
    <div className="card-body p-9">
      {/*begin::Row*/}
    
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Name</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          <input
            type="text"
            className="form-control form-control-solid"
            name="name"
            defaultValue=""
            value={values.name}
            onChange={handleChange}
          />
        </div>
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Datasource</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        {console.log("props?.datasourcemonitor",props?.datasourcemonitor)}
        <div className="col-xl-6  " style={{display:'flex'}}>
          <input
            type="text"
            className="form-control form-control-solid"
            name="datasource"
            disabled={true}
            defaultValue=""
            value={props?.datasourcemonitor?.name|| ''}
            onChange={handleChange}
          />
         
         
              </div>
              <a href='#' style={{padding:"12px",width:'150px'}}className='btn btn-sm fw-bold btn-primary' data-bs-toggle='modal' data-bs-target='#kt_modal_upgrade_plan'>
          Select Data Source
              </a>
       
        
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Query</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          <textarea
            name="query"
            className="form-control form-control-solid h-100px"
            defaultValue=''
            value={values.query}
            onChange={handleChange}
          />
        </div>
        {/*begin::Col*/}
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Categories</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          
          <select
  name="categories"
  data-control="select2"
  data-placeholder="Select a Category..."
  className="form-select form-select-solid select2-hidden-accessible"
  data-select2-id="select2-data-9-4ka5"
  tabIndex={-1}
  aria-hidden="true"
  data-kt-initialized={1}
  value={values.categories}
  onChange={handleChange}
>
  <option value="CRITICAL" data-select2-id="select2-data-11-tdo3">
  Critical
  </option>
  <option value="HIGH" data-select2-id="select2-data-127-ovaw">
   High
  </option>
  <option value="MEDIUM" data-select2-id="select2-data-128-6gou">
    Medium
  </option>
  <option value="LOW" data-select2-id="select2-data-129-m3ln">
    Low
  </option>
</select>

        </div>
      </div>
      {/* <div className="row">
        
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Status</div>
        </div>
      
        <div className="col-xl-9">
          <div className="form-check form-switch form-check-custom form-check-solid">
            <input
              className="form-check-input"
              type="checkbox"
              defaultValue=""
              id="status"
              name="status"
              defaultChecked="checked"
            />
            <label
              className="form-check-label fw-semibold text-gray-400 ms-3"
              htmlFor="status"
            >
              Active
            </label>
          </div>
        </div>
      
      </div> 
      <div className="row mb-8">

        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Created Date</div>
        </div>
      
        <div className="col-xl-9 fv-row">
          <div className="position-relative d-flex align-items-center">
            <i className="ki-duotone ki-calendar-8 position-absolute ms-4 mb-1 fs-2">
              <span className="path1" />
              <span className="path2" />
              <span className="path3" />
              <span className="path4" />
              <span className="path5" />
              <span className="path6" />
            </i>
            <input
              className="form-control form-control-solid ps-12"
              name="createddate"
              placeholder="Pick a date"
              id="kt_datepicker_1"
            />
          </div>
        </div>
        
      </div> */}
     
    </div>
    {/*end::Card body*/}
    {/*begin::Card footer*/}
    <div className="card-footer d-flex justify-content-end py-6 px-9">
      <button
        type="reset"
        className="btn btn-light btn-active-light-primary me-2"
      >
        Discard
      </button>
      <button
        type="submit"
        className="btn btn-primary"
        id="kt_project_settings_submit"
        onClick={createHandler}
      >
        Create 
        <ToastContainer />
      </button>
    </div>
    {/*end::Card footer*/}
  </form>
  {/*end:Form*/}
</div>

    </>);
};
const mapStateToProps = (state) => {
  return {
    datasourcemonitor: state.Dataoperation.datasourcemonitor,
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    addDatasourcemonitor: (s) => {
      dispatch(addDatasourcemonitor(s))
    }
   
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CreateMonitoringDashboard);

